body {
  font-family: 'Noto Sans JP', sans-serif; /* Use Noto Sans JP for a cleaner look */
  background-color: #000000; /* Black for the background */
  color: #FFFFFF; /* White text for contrast */
  text-align: center;
  height: 100vh; /* Full viewport height */
  display: flex;
  flex-direction: column; /* Allow elements to stack vertically */
  justify-content: center; /* Centers content vertically */
  align-items: center; /* Centers content horizontally */
  padding: 20px; /* Prevent elements from touching the edges */
}

button {
  font-size: 1.25em; /* Larger font size for better readability */
  padding: 20px 40px; /* Larger buttons */
  margin: 20px; /* More space around buttons */
  width: auto; /* Auto width based on content */
  border-radius: 30px; /* Rounded borders */
  background-color: #D32F2F; /* Red background for the button */
  color: #FFFFFF; /* White text for contrast */
  border: none;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3); /* Subtle shadow for depth */
  cursor: pointer; /* Ensure buttons have a pointer cursor */
}

button:hover {
  background-color: #B71C1C; /* Darker red on hover */
  box-shadow: 0px 8px 16px rgba(0, 0, 0, 0.5); /* Larger shadow on hover */
}

/* Enlarged input fields */
select {
  font-size: 1.2em; /* Larger font size */
  padding: 10px 20px; /* More padding */
  border-radius: 10px; /* Rounded borders */
  border: 2px solid #4CAF50; /* Green border */
  color: #000000; /* Black text for readability */
  background-color: #FFFFFF; /* White background for dropdown */
  cursor: pointer; /* Pointer cursor for dropdown */
  margin-top: 20px; /* Space from preceding elements */
}

/* Container adjustments */
.container {
  width: 60%; /* Uses more space horizontally */
  max-width: 640px; /* But won't grow too large on big screens */
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.food-container,
.date-container,
.confirmation {
  animation: fadeIn 1s ease-out;
}
